<template>
  <div class="home">
    <header-page 
      :fixed="true"
    ></header-page>

    <div class="p-3" style="position: relative;">
      <div class="block-message-plan">
        <span class="text-white text-block-message">Hi, {{name}}! ðŸ‘‹ </span>
        <p class="text-white text-block-message">You are currently using a limited version of our service.
          Upgrade your plan to unlock all features.</p>

        <button class="button-block-message">Upgrade</button>
      </div>

      <div class="block-projects">
        <h5 class="text-bold">My Projects</h5>

        <div class="project">
          <div class="create-new-project" @click="createProject">
            <div class="square-block">
              <span class="header-create-project">+</span>
              <span class="text-create-project">New Project</span>
            </div>
          </div>

          <div v-for="project in projects" :key="project.id" class="project-elem">
            <div 
              class="square-block" 
              :style="{
                justifyContent: 'space-between',
                backgroundImage: `url('${project.preview_image_url}')`,
                backgroundPosition: 'center center',
                borderRadius: '6px',
                alignItems: 'flex-end',
              }"
            >
              <div class="project-settings">
                <b-dropdown 
                    :id="'dropdown-'+project.id" 
                    toggle-class="text-decoration-none bg-transparent border-0 m-0 p-0"
                    menu-class="border-0"
                    size="lg"
                    offset="0,10"
                    no-caret
                    right
                    no-flip
                >
                  <template v-slot:button-content>
                    <img src="../assets/img/Circle-setting.png" alt="">
                  </template>

                  <b-dropdown-item>
                    <router-link to='/editor'>
                      <img class="img-menu" src="../assets/img/Edit.png" alt="">
                      Edit
                    </router-link>
                  </b-dropdown-item>
                  <b-dropdown-item>
                    <img class="img-menu" src="../assets/img/Settings.png" alt="">
                    Settings
                  </b-dropdown-item>
                  <b-dropdown-item @click="delProject(project.id)">
                    <img class="img-menu" src="../assets/img/Delete.png" alt="">
                    Delete
                  </b-dropdown-item>
                </b-dropdown>
              </div>

              <div class="project-name">{{project.caption}}</div>
            </div>
          </div>
        </div>
      </div>

      <popup-create-project 
        v-if="crate_project"
        :close="createProject"
      ></popup-create-project>

      <div class="warning-del" v-if="warning_del">
        <div class="warning-content p-4">
          <h6 class="text">Are you sure that you want to delete</h6>
          <h6><b>{{name_project}} ?</b></h6>
          <span>This action cannot be undone.</span>

          <div class="button-block">
            <button class="button button-del">
              <img src="" alt="">
              Delete
            </button>

            <button @click="delProject" class="button button-cancel">Cancel</button>
          </div>
        </div>
      </div>
    </div>      
  </div>
</template>

<script>
import axios from '../Scripts/service/axios.js';

export default {
  data() {
    let data = {
      name: 'John Marston',
      crate_project: false,
      warning_del: false,
      name_project: undefined,

      projects: undefined,
    }
    return data;
  },
  mounted() {
    axios.post('projects/').then(res => {
      this.projects = res.data.pages;
    })
    .catch(err => {
      console.error(err)
    })
  },
  methods: {
   delProject(name) {
     this.warning_del = !this.warning_del;
     this.name_project = name;
     document.getElementsByTagName('body')[0].style.overflow = this.warning_del?'hidden':''
   },
   createProject() {
     this.crate_project = !this.crate_project;
     document.getElementsByTagName('body')[0].style.overflow = this.crate_project?'hidden':''
   }
  }

}
</script>

